import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Button from '@material-ui/core/Button';
import FacebookIcon from '@material-ui/icons/Facebook';

const appId = process.env.REACT_APP_RADAR_FACEBOOK_APPID;
const apiVersion = process.env.REACT_APP_RADAR_FACEBOOK_VERSION;

window.fbAsyncInit = () => {
	window.FB.init({
		appId      : appId,
		cookie     : true,
		xfbml      : true,
		version    : apiVersion,
	});
}

function loadFacebookScript() {
	const facebookScriptID = 'facebook-jssdk';
	if (!document.getElementById(facebookScriptID)) {
		const facebookScript = document.createElement('script');
		facebookScript.id = facebookScriptID;
		facebookScript.setAttribute('async', true);
		facebookScript.setAttribute('defer', true);
		facebookScript.setAttribute('crossorigin','anonymous');
		facebookScript.src = `https://connect.facebook.net/en_US/sdk.js#xfbml=1&cookie=1&version=${apiVersion}&appId=${appId}`;
		const app = document.getElementById('root');
	 	app.parentNode.insertBefore(facebookScript, app);
	}
}
window.onload(loadFacebookScript);

export default function FacebookSignup(props) {
	const {
		setSignupEmail,
		setBusy,
		...rest
	} = props;
	const classes = makeStyles((theme) => ({
		facebookButton: {
			color: 'white',
			backgroundColor: '#4267B2',
		},
	}))();
	const [isSignedIn, setIsSignedIn] = React.useState(false);

	function facebookLogin() {
		if ('FB' in window) {
			setBusy(true);
			window.FB.AppEvents.logPageView();
			window.FB.getLoginStatus(response => {
				if (response.status === 'connected') {
					setIsSignedIn(true);
				} else {
					window.FB.login(response => {
						response.status === 'connected' && setIsSignedIn(true);
					}, {scope: 'email,public_profile', return_scopes: true});
				}
			});
		} else {
			console.error('facebook sdk script not loaded');
		}
	}

	function requestProfile(response) {
    if (response && !response.error) {
    	setSignupEmail(response.email);
    } else {
      console.error(response.error);
    }
	}

	React.useEffect(() => {
		if (isSignedIn) {
			window.FB.api(
				`/me/`,
				'get',
				{fields: 'email'},
		    requestProfile
			);
		}
	}, [isSignedIn]);

	return (
		<React.Fragment>
			<Button
				className={classes.facebookButton}
				variant="contained"
				onClick={facebookLogin}
				startIcon={<FacebookIcon size="medium" />}
			>
				Facebook login
			</Button>
    </React.Fragment>	
	)
}